.mainWrapper {
    padding: 10px;
}

.wrapper {
    display: grid;
    grid-template-columns: 75px auto 100px;
    grid-template-rows: 1fr;
    background-color: rgb(173, 232, 255);
    color: rgb(56, 105, 124);
    border-radius: 10px;
    width: auto;
    height: 100px;
    padding: 10px;
    margin-bottom: 10px;
}

.userPhoto {
    display: block;
    border-radius: 50px;
    width: 65px;
    height: 65px;
    margin-bottom: 10px;
}

.followButton {
    background-color: rgb(108, 177, 247);
    color: white;
    outline: none;
    border-style: none;
    text-align: center;
    width: 65px;
    height: 25px;
    border-radius: 5px;
    transition-duration: 0.5s;
}

.unfollowButton {
    background-color: rgb(223, 96, 96);
    color: white;
    outline: none;
    border-style: none;
    text-align: center;
    width: 65px;
    height: 25px;
    border-radius: 5px;
    transition-duration: 0.5s;
}

.disabledButton {
    background-color: rgb(243, 243, 243);
    color: rgb(122, 122, 122);
    outline: none;
    border-style: none;
    text-align: center;
    width: 65px;
    height: 25px;
    border-radius: 5px;
    transition-duration: 0.5s;
}

.followButton:hover, .unfollowButton:hover {
    filter: brightness(90%);
    transition-duration: 0.5s;
    cursor: pointer;
}

.leftBlock {
    grid-column: 1;
    grid-row: 1;
}

.middleBlock {
    grid-column: 2;
    grid-row: 1;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 3fr;
    padding: 0px 10px 0px 10px;
}

.rightBlock {
    grid-column: 3;
    grid-row: 1;
}

.userName {
    grid-column: 1;
    grid-row: 1;
    font-size: large;
    font-weight: bold;
}

.userStatus {
    grid-column: 1;
    grid-row: 2;
}

.userLocation {
    text-align: right;
    word-wrap: break-word;
    padding: 5px;
}

.pageSelector {
    display: inline-block;
    background-color: cornflowerblue;
    text-align: center;
    width: auto;
    height: auto;
    padding-left: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
    user-select: none;
}

.selector {
    display: inline;
    cursor: pointer;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 24px;
    margin-right: 10px;
}

.active {
    color: rgb(0, 0, 0);
}

.paginator button {
    outline: none;
    border: none;
    border-radius: 10px;
    padding: 5px;
    margin: 5px;
    font-size: 16px;
    background-color: rgb(171, 192, 230);
}